/**
 * @name ju-tab
 * @description 标签页组件
 * @author  GBA
 * @date 2016/06/08
 */

class juTab {
    // 构造函数
    constructor(query, options) {
        if (!query) {
            try {
                this.mytab = document.querySelector('.ju-tab')
                this.mycontent = document.querySelector('.ju-tab-content')
            } catch (e) {
                console.log(e)
                return false
            }
        } else {
            try {
                this.mytab = document.querySelector(query)
                this.mycontent = document.querySelector(query + '-content')
            } catch (e) {
                console.log(e)
                return false
            }
        }

        this.mytabs = this.mytab.querySelectorAll('.ju-tab-li') || this.mytab.querySelectorAll('li')
        this.mycontents = this.mycontent.querySelectorAll('.ju-tab-content-li')
            || this.mycontent.querySelectorAll('li')

        this.config = {
            'autoLayout': true,
            'materialAnimate': true
        }
        // if (this.isJson(options)) {
            Object.assign(this.config, options)
        // }

        if (this.mytabs && this.mycontents) {
            this.render()
            return true
        } else {
            console.log('选项卡或选项卡内容为空')
            return false
        }
    }

    // 为标签添加事件
    render() {
        const tabs = this.mytabs
        const contents = this.mycontents
        for (let i = 0; i < tabs.length; i++) {
            if (this.config.autoLayout) {
                tabs[i].style.width = ('' + 100/tabs.length + '%')
            }

            tabs[i].onclick = function () {
                for (let j = 0; j < contents.length; j++) {
                    if (i === j) {
                        contents[j].style.display = 'block'
                        // tabs[i].addClass('active')
                        tabs[j].classList.add('active')
                    } else {
                        contents[j].style.display = 'none'
                        tabs[j].classList.remove('active')
                    }
                }
            }
        }
    }

    // 转换convert a string containing JSON notation into a Javascript object.
    isJson(str) {
        try {
            JSON.parse(str)
        } catch (e) {
            return false
        }
        return true
    }
// end of juTab
}
